<template>
  <div class="basic">
    <div class="form-container">
      <el-form label-position="left" label-width="100px">
        <el-form-item label="">
          <el-input v-model="formData.nameCn" placeholder="" required></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.nameEn" placeholder="" required></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.homeUrl" placeholder="">
            <template slot="prepend">http://</template>
          </el-input>
        </el-form-item>
        <el-form-item label="logo">
          <el-upload class="upload-box" name="logo" action="/api/uploadFile" :on-success="onLogoSuccess" :show-file-list="false">
            <i class="el-icon-plus"></i>
            <img :src="formData.logoUrl" alt="">
          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <el-upload class="upload-box" name="picture" action="/api/uploadFile" :on-success="onBannerSuccess" :show-file-list="false">
            <i class="el-icon-plus"></i>
            <img :src="formData.bannerUrl" alt="">
          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.shortDesc" placeholder=""></el-input>
        </el-form-item>
        <el-form-item class="tag-item" label="">
          <el-tag class="tag-text" :key="index" v-for="(tag, index) in formData.tagList" closable :disable-transitions="false" @close="removeTag(index)"> {{tag}} </el-tag>
          <el-input v-if="tagInputVisible" class="tag-input" ref="tagInput" v-model="inputTag" size="small" @keyup.enter.native="addTag" @blur="addTag" >
          </el-input>
            <el-button v-else class="tag-btn" size="small" @click="showTagInput">+ </el-button>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="formData.abstract" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload class="upload-box" name="whitePaper" action="/api/uploadFile" :on-success="onWhitePaperSuccess" :show-file-list="false">
            <i class="el-icon-plus"></i>
            <img :src="formData.whitePaperUrl" alt="">
          </el-upload>
        </el-form-item>
        <el-form-item :label="select.label" v-for="(select, field) in selectList" :key="field">
          <el-select v-model="formData[field]">
            <el-option v-for="(option, index) in select.optionList" :key="option.value" v-if="index" :label="option.label" :value="option.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker v-model="formData.fundStartTime" type="datetime" default-value="2018-01-01T00:00:00" placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker v-model="formData.fundEndTime" type="datetime" default-value="2018-01-01T00:00:00" placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="formData.fundStage">
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
            <el-option label="" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.tokenName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.tokenSymbol" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.tokenPrice" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.contractAddr" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.companyEmail" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.companyAddr" placeholder=""></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="footer-btn-area">
      <el-button type="primary" @click="submit"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      inputTag: '',
      tagList: [],
      tagInputVisible: false,
      selectList: {},
      formData: {
        projId: '',
        nameCn: '',
        nameEn: '',
        shortDesc: '',
        abstract: '',
        homeUrl: '',
        logoUrl: '',
        bannerUrl: '',
        tagList: '',
        whitePaperUrl: '',
        region: '',
        buzType: '',
        stage: '',
        foundDate: '',
        fundStartTime: '',
        fundEndTime: '',
        fundStage: '',
        companyEmail: '',
        companyAddr: '',
        tokenName: '',
        tokenSymbol: '',
        tokenPrice: '',
        contractAddr: ''
      }
    }
  },
  mounted () {
    this.formData.projId = this.$route.params.id
    this.$http.get('/api/getProjTagList').then((res) => {
      if (res.data.errcode === 0) {
        this.selectList = res.data.data
      }
    })
    this.updateData()
  },
  methods: {
    updateData: function () {
      this.$http.post('/api/getProjBasicInfo', {
        projId: this.formData.projId
      }).then((res) => {
        if (res.data.errcode === 0) {
          this.formData = res.data.data
          this.formData.projId = res.data.data.id
        }
      })
    },
    onLogoSuccess (res) {
      if (res.errcode === 0) {
        this.formData.logoUrl = res.data.url
      }
    },
    onBannerSuccess (res) {
      if (res.errcode === 0) {
        this.formData.bannerUrl = res.data.url
      }
    },
    onWhitePaperSuccess (res) {
      if (res.errcode === 0) {
        this.formData.whitePaperUrl = res.data.url
      }
    },
    showTagInput () {
      this.tagInputVisible = true
      this.$nextTick(_ => {
        this.$refs.tagInput.$refs.input.focus()
      })
    },
    addTag () {
      if (this.inputTag) {
        this.formData.tagList.push(this.inputTag)
      }
      this.tagInputVisible = false
      this.inputTag = ''
    },
    removeTag (index) {
      this.tagList.splice(index, 1)
    },
    submit () {
      this.$http.post('/api/updProjBasicInfo', this.formData).then((res) => {
        if (res.data.errcode === 0) {
          this.$message({ type: 'success', message: '!' })
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.form-container {
  padding: 0 20px;
  .tag-item {
    .tag-text {
      margin-right: 10px;
    }
    .tag-input {
      width: 90px;
    }
  }
}
</style>



